<template>
  <div>
    <div class="miniheader">
      <div class="container">
        <div class="miniheader-logo">
          <router-link to="/">小米官网</router-link>
        </div>
        <div class="miniheader-title">
          <h2>支付订单</h2>
          <p>温馨提示：产品是否购买成功，以最终下单为准哦，请尽快结算</p>
        </div>
        <div class="miniheader-info">
          <a href="#" class="user-name">
            <span>{{user.name}}</span>
          </a>
          <span class="sep">|</span>
          <a href="javascript:;" class="user-name" @click="confirmQuit">
            <span>注销</span>
          </a>
          <!-- <a href="#" class="info-order">我的订单</a> -->
        </div>
      </div>
    </div>

    <div class="confirm">
      <div class="container">
        <form action="post">
          <div class="confirm-order confirm-section clearfix">
            <i class="iconfont">&#x221a;</i>
            <div class="box-order clearfix">
              <div class="order-info">
                <h2 class="info-title">订单提交成功！去付款咯～</h2>
                <p class="info-time">
                  请在
                  <span>0小时30分</span>内完成支付, 超时后将取消订单
                </p>
                <p class="info-address">
                  <span class="address"></span>
                </p>
              </div>
              <div class="order-total">
                <p>
                  应付总额：
                  <span>
                    <em class="money">{{orderInfo.sum}}</em>元
                  </span>
                </p>
                <a href="javascript:void(0);" @click="detailVisible=!detailVisible">
                  <el-button>订单详细</el-button>
                </a>
              </div>
            </div>

            <div class="order-detail" v-if="detailVisible">
              <ul>
                <li class="clearfix">
                  <div class="label">订单号：</div>
                  <div class="content order-no">{{orderInfo.order_no}}</div>
                </li>
                <li class="clearfix">
                  <div class="label">收货信息：</div>
                  <div class="content address">{{addressInfo}}</div>
                </li>
                <li class="clearfix">
                  <div class="label">商品名称：</div>
                  <div
                    class="content items"
                    v-for="(item,index) in orderInfo.items"
                    :key="`item_${index}`"
                  >
                    名称：{{item.name}} | 版本：{{item.prop1_value}} | 颜色：{{item.prop2_value}} | 数量：{{item.quantity}} | {{item.price}}元
                    <br>
                  </div>
                </li>
              </ul>
            </div>
          </div>

          <div class="confirm-payment confirm-section">
            <div class="payment-title">选择以下支付方式付款</div>
            <div class="payment-list">
              <ul class="clearfix">
                <li id="pay" @click="goToPay">
                  <img src="../img/weixinpay.png" alt>
                </li>
                <!-- <li><img src="img/payOnline_zfb.png" alt=""></li>
                <li><img src="img/unionpay.png" alt=""></li>-->
              </ul>
            </div>
          </div>
        </form>
      </div>
      <div class="confirm-qrcode" v-if="qrcodeVisble" @click="qrcodeVisble=false">
        <div id="qrcode"></div>
      </div>
    </div>
  </div>
</template>

<script>
import api from "../../utils/api";
import QRCode from "qrcodejs2";

import { mapState, mapActions } from "vuex";
export default {
  data() {
    return {
      orderInfo: "",
      detailVisible: false, //订单详细可见
      qrcodeVisble: false
    };
  },
  created() {
    //   先登录
    // this.checklogin()
    //   .then(data => {
        console.log("现在是登录状态");
        const orderNo = this.$route.params.order_no;
        api.get("/user/getOrder?order_no=" + orderNo).then(data => {
          this.orderInfo = data;
          console.log(data);
        });
      // })
      // .catch(() => {
      //   console.log("现在是没登录状态");
      // });
  },
  computed: {
    ...mapState("user", ["user"]),
    addressInfo() {
      console.log(this.orderInfo);

      const addr = this.orderInfo.address;
      return `${addr.recipient}    ${addr.phone}  
            ${addr.area} ${addr.address}
          `;
    }
  },
  methods: {
    ...mapActions("user", ["logout"]),
    goToPay() {
      api
        .post("/user/pay", { order_no: this.orderInfo.order_no })
        .then(data => {
          // 生成二维码
          this.qrcodeVisble = true;
          //   二维码;
          this.$nextTick(() => {
             new QRCode("qrcode", {
              width: 300, // 设置宽度，单位像素
              height: 300, // 设置高度，单位像素
              text: data // 设置二维码内容或跳转地址
            });
          });
        });
    },
    confirmQuit() {
      this.$confirm("确认退出登录？")
        .then(() => {
          this.logout();
          this.$router.push("/login");
        })
        .catch(() => {});
    }
  }
};
</script>

<style>
@import url("../css/confirm.css");
@import url("../css/iconfont.css");
@import url("../css/style.css");
</style>
